import React, {useState} from 'react'
import { Card, Table, Menu, Modal, message, Row, Col, Form, Select, InputNumber, Tag, DatePicker} from 'antd';
import Flex from 'components/shared-components/Flex'
import { Typography } from "antd";
import { useCollection } from "react-firebase-hooks/firestore";
import { DataService } from "services/trip.service";
import { DataService as RoadTallService } from "services/roadtalls.service";
import { DataService as GarageService} from "services/garage.service";
import NumberFormat from 'react-number-format';
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown';
import { DeleteOutlined, NodeExpandOutlined, ExclamationCircleOutlined, CheckCircleOutlined, EyeOutlined } from '@ant-design/icons';
import moment from 'moment';
import Invoice from './invoice';

const { Title } = Typography;
const dateFormat = 'DD/MM/YYYY';
const { confirm } = Modal;
const { Option } = Select;
const getStatus = status => {
	if(status == 'Pending') {
		return <Tag className ="text-capitalize" color="#108ee9">Pending</Tag>
	}
	else if(status == 'En Route') {
		return <Tag className ="text-capitalize" color="#ffc542">En Route</Tag>
	}
	else if(status == 'Delivered') {
		return <Tag className ="text-capitalize" color="#87d068">Delivered</Tag>
	}
	else{
		return <Tag className ="text-capitalize" color='red'>Cancelled</Tag>
	}
}

const TripHistoric = ({systemData}) => {

	const { allDestinationsLoading, allDestinations,  allTrucks, borders, bordersLoading, currencies, currenciesLoading, trips, tripsLoading, orders, ordersLoading,  allRentedTrucks, allCurrencyLoading, allCurrency, garages, garagesLoading, allTrips, allTripsLoading, rounder, allCountries, allCountriesLoading, roadTall, fuels, allBorders} = systemData;
	const [visible, setVisible] = useState(false);
	const [confirmLoading, setConfirmLoading] = useState(false);
	const [trip, setTrip] = useState({});
	const [order, setOrder] = useState({});
	const [form] = Form.useForm();
	const [dates, setDates] = useState(new Date());
	const [rate, setRate] = useState(0);
	const [price, setPrice] = useState(0);
	const [totalAmount, setTotalAmount] = useState(0);
	const [showInvoice, setShowInvoice] = useState(false);
	const [roadTalls, setRoadTalls] = useState([]);
	const [orderGarage, setOrderGarage] = useState([]);
	const [orderFuel, setOrderFuel] = useState([]);
	
	let allRecords = [];
  	if(!ordersLoading){
		orders.docs.map((doc)=>{
			allRecords = {
				...allRecords,
				[doc.data()._id] : {'client':doc.data().client, 'item':doc.data().item, 'departure':doc.data().departure, 'destination':doc.data().destination},
			}
		})
	}

	const showDetails = (trip) => {
		setTrip(trip);
		setOrder(allRecords[trip.orderId]);
		let talls = roadTall.docs.filter(x=> x.data().tripId == trip._id);
		let fls = fuels.docs.filter(x=> x.data().tripId == trip._id);
		let grg = garages.docs.filter(x=> x.data().tripId == trip._id);
		
		setRoadTalls(talls);
		setOrderFuel(fls);
		setOrderGarage(grg);
		setShowInvoice(true);
	};


	const handleAmountChange = (value) => {
		let total = rate*value;
		setPrice(value);
		setTotalAmount(total);
	}

	const handleRateChange = (value) => {
		let total = value*price;
		setRate(value);
		setTotalAmount(total);
	}

	const dropdownMenu = row => (
		row.status == 'En Route' ? <Menu>
			<Menu.Item onClick={() => {showDetails(row)}}>
				<Flex alignItems="center">
					<EyeOutlined />
					<span className="ml-2">View Details</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => addRoadTall(row)}>
				<Flex alignItems="center">
					<NodeExpandOutlined />
					<span className="ml-2">Add Road tall</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => markDelivered(row)}>
				<Flex alignItems="center">
					<CheckCircleOutlined />
					<span className="ml-2">Mark as delivered</span>
				</Flex>
			</Menu.Item>
			<Menu.Item onClick={() => confirmDelete(row)}>
				<Flex alignItems="center">
					<DeleteOutlined />
					<span className="ml-2">Delete</span>
				</Flex>
			</Menu.Item>
		</Menu> : <Menu><Menu.Item onClick={() => {showDetails(row)}}>
				<Flex alignItems="center">
					<EyeOutlined />
					<span className="ml-2">View Details</span>
				</Flex>
			</Menu.Item></Menu>
	);

	const addRoadTall = (row) => {
		setTrip(row);
		setVisible(true);
	};

	const handleDateChange = date => {
		setDates(date ? new Date(date.format('YYYY-MM-DD HH:mm:ss')) : new Date());
	};

  	const disabledDate = (current) => {
		return current && current > moment().endOf("day");
	} 

	const handleOk = async e => {
		try {
		  const values = await form.validateFields();
		  let border = values.border;
		  let currency = values.currency;
		  let amount = values.amount;
		  let exchangeRate = values.exchangeRate;
		  if (border === '' || amount === '' || currency === '' || exchangeRate === '' || !totalAmount) return;
	
		  setConfirmLoading(true);
		  let roadTallData = { 
			border, 
			amount, 
			exchangeRate, 
			currency, 
			totalAmount, 
			tripId:trip._id, 
			truckId:trip.truckId, 
			internal:trip.internal, 
			orderId:trip.orderId 
		}
		  RoadTallService.create(roadTallData,+(dates.getTime()));
		  handleCancel();
		  message.success(`Road tall info added`);
		  
		} catch (errInfo) {}
		
	  };

	const handleCancel = () => {
		setVisible(false);
		setConfirmLoading(false)
	}

	const confirmDelete = (row) => {
		confirm({
		  title: 'Do you Want to delete this trip?',
		  icon: <ExclamationCircleOutlined />,
		  onOk() {
			remove(row._id);
		  }
		});
	};
	
	const markDelivered = (row) => {
		confirm({
		  title: 'Do you Want to mark this trip as delivered?',
		  icon: <ExclamationCircleOutlined />,
		  onOk() {
			delivered(row);
		  }
		});
	};

	const remove = key => {
		DataService.remove(key)
		message.success(`Trip info deleted`);
	};

	const delivered = row => {
		
		if(!garagesLoading){
			garages.docs.filter(x=> +x.data().truckId == row.truckId && !x.data().orderId).forEach(el => {
				GarageService.update(el.data()._id,{orderId:row.orderId, tripId:row._id});
			});
		}
		DataService.update(row._id,{'status':'Delivered'});
		message.success(`Trip have delivered`);
	};
	

  

  	

	const tableColumns = [
		{
			title: 'Date',
			dataIndex: 'id',
			render: (_,record) => new Date(+record.data()._id).toLocaleString()
		},
		{
			title: 'Truck',
			dataIndex: 'id',
			render: (_,record) => record.data().internal ? 
			`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
			:
			`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`
		},
		{
			title: 'Milleage',
			dataIndex: 'milleage',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().milleage}
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'RoadTall',
			dataIndex: 'roadtall',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={allTripsLoading ? '' : allTrips[record.data()._id]?.roadTallAmount} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Fuel',
			dataIndex: 'fuel',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={allTripsLoading ? '' : allTrips[record.data()._id]?.fuelAmount} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Garage / Others',
			dataIndex: 'garage',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={allTripsLoading ? '' : allTrips[record.data()._id]?.garageAmount} 
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Revenue',
			dataIndex: 'revenue',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={record.data().totalAmount}
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Expenses',
			dataIndex: 'expenses',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={allTripsLoading ? '' : allTrips[record.data()._id]?.expenses}
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		{
			title: 'Balance',
			dataIndex: 'balance',
			render: (_, record) => (
					  <div>
						  <NumberFormat
							  displayType={'text'} 
							  value={allTripsLoading ? '' : rounder(allTrips[record.data()._id]?.balance)}
							  thousandSeparator={true} 
						  />
					  </div>
				  ),
		},
		// {
		// 	title: 'Departure',
		// 	dataIndex: 'status',
		// 	render: (_, record) => (
		// 		<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().departure]?.name +' - '+allDestinations[record.data().departure]?.country}</Flex>
		// 	),
		// },
		{
			title: 'Destination',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{allDestinationsLoading ? '' : allDestinations[record.data().destination]?.name +' - '+allDestinations[record.data().destination]?.country}</Flex>
			),
		},
		{
			title: 'Status',
			dataIndex: 'status',
			render: (_, record) => (
				<Flex alignItems="center">{getStatus(record.data().status)}</Flex>
			),
		},
		{
			title: '',
			dataIndex: 'actions',
			render: (_, elm) => (
				<div className="text-right">
					<EllipsisDropdown menu={dropdownMenu(elm.data())}/>
				</div>
			)
		}
	];

	return (
		<Form form={form} component={false} layout="horizontal">
		<Card className={(showInvoice) ? 'd-none' : ''}>
			<Flex alignItems="center" justifyContent="between" mobileFlex={false}>
				<Flex className="mb-1" mobileFlex={false}>
					<div className="mr-md-3 mb-3">
						<Title level={4}>Trips list</Title>
					</div>
				</Flex>
			</Flex>
			<div className="table-responsive">
				<Table 
					columns={tableColumns} 
					dataSource={!tripsLoading ? trips.docs.reverse() : []} 
					loading={tripsLoading}
					rowKey='_id' 
				/>
			</div>
		</Card>
		<Modal
              title="New Road Tall details"
              visible={visible}
              onOk={handleOk}
              confirmLoading={confirmLoading}
              onCancel={handleCancel}
            >
              <Row gutter={16}>
			  	<Col xs={24} sm={12} md={12}>
                  <Form.Item name="dates" label="Date added" rules={[
                    {
                      required: true,
                      message: `When roadtall were payed?`,
                    },
                  ]}>
                    <DatePicker className="w-100" defaultValue={moment(dates, dateFormat)} format={dateFormat} disabledDate={disabledDate} onChange={handleDateChange}/>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
                  <Form.Item name="border" label="Border name" rules={[
                    {
                      required: true,
                      message: `border is required.`,
                    },
                  ]}>
                    <Select
							className="w-100"
							showSearch
							placeholder="Select border"
							optionFilterProp="children"
						>
						{!bordersLoading && borders.docs.map((border, index) => (
							<Option key={index} value={border.data()._id}>
								{border.data().name+' - '+border.data().country}
							</Option>
						))}
					</Select>
                  </Form.Item>
                </Col>
                <Col xs={24} sm={12} md={12}>
					<Form.Item name="amount" label="Amount paid" rules={[
                    {
                      required: true,
                      message: `enter amount paid on border`,
                    }]}>
						<InputNumber
							className="w-100"
							placeholder="Enter amount"
							onChange={handleAmountChange}
							parser={value => value.replace(/\$\s?|(,*)/g, '')}
							formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
						/>
					</Form.Item>
                </Col>
				<Col xs={24} sm={12} md={12}>
					<Form.Item name="currency" label="Select currency used" rules={[
                    {
                      required: true,
                      message: `enter currency used`,
                    }]}>
						<Select
							className="w-100"
							showSearch
							placeholder="Select currency used"
							optionFilterProp="children"
						>
						{!currenciesLoading && currencies.docs.map((currency, index) => (
							<Option key={index} value={currency.data()._id}>
								{currency.data().name} {`              `} &nbsp;
							</Option>
						))}
						</Select>
					</Form.Item>
                </Col>
				<Col xs={24} sm={12} md={12}>
					<Form.Item name="exchangeRate" label="Exchange rate vs 1RWF" rules={[
                    {
                      required: true,
                      message: `enter exchange rate`,
                    }]}>
						<InputNumber
							className="w-100"
							onChange={handleRateChange}
							parser={value => value.replace(/\$\s?|(,*)/g, '')}
							formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
						/>
					</Form.Item>
                </Col>
				<Col xs={24} sm={12} md={12}>
					<Form.Item label="Total Paid in RWF">
						<InputNumber
							className="w-100"
							min={0}
							value={totalAmount}        
							parser={value => value.replace(/\$\s?|(,*)/g, '')}
							formatter={value => `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
						/>
					</Form.Item>
				</Col>
              </Row>
            </Modal> 
			{showInvoice &&(<Invoice 
				systemData={systemData} 
				roadTalls={roadTalls} 
				order={order} 
				trip={trip}
				allDestinationsLoading={allDestinationsLoading} 
				allDestinations={allDestinations} 
				allCurrencyLoading={allCurrencyLoading} 
				allCurrency={allCurrency}
				allTrucks={allTrucks}
				allTrips={allTrips}
				allBorders={allBorders}
				orderFuel={orderFuel}
				orderGarage={orderGarage}
				allCountries={allCountries}
				allCountriesLoading={allCountriesLoading}
				allRentedTrucks={allRentedTrucks}
				rounder={rounder}
			/>)}
		</Form>
	)
}

export default TripHistoric
