import React, {useState} from 'react'
import { PrinterOutlined, CloseOutlined, CheckOutlined, CloseCircleOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import { Card, Table, Button, Tag, Alert } from 'antd';
import NumberFormat from 'react-number-format';
import { useHistory } from "react-router-dom";
import Flex from 'components/shared-components/Flex'
 
const { Column } = Table; 
const Invoice = (props) => { 	
	let history = useHistory();	
	const RedirectBack = () => {
		history.push(`/app/apps/orders`)
	}
	const getStatus = status => {
		if(status == 'Pending') {
			return <Tag className ="text-capitalize" color="#108ee9">Pending</Tag>
		}
		else if(status == 'En Route') {
			return <Tag className ="text-capitalize" color="#ffc542">En Route</Tag>
		}
		else if(status == 'Delivered') {
			return <Tag className ="text-capitalize" color="#87d068">Delivered</Tag>
		}
		else{
			return <Tag className ="text-capitalize" color='red'>Cancelled</Tag>
		}
	}
	
	
	const { systemData, order, orderTrips, allCurrencyLoading, allCurrency, allDestinationsLoading, allDestinations, allTrucks, roadTalls, allBorders, orderFuel, orderGarage, allCountries, allCountriesLoading, allRentedTrucks, trip, rounder } = props;
	const { userData, allTrips, allTripsLoading } = systemData;
	let orderPrice = +trip.totalAmount;
	let totalMilleage = +trip.milleage;
	let totalRoadtall = roadTalls.reduce((accumulator, currentValue) => accumulator + (currentValue.data().exchangeRate*currentValue.data().amount), 0);
	let totalFuel = orderFuel.reduce((accumulator, currentValue) => accumulator + currentValue.data().totalAmount, 0);
	let totalGarage = orderGarage.reduce((accumulator, currentValue) => accumulator + currentValue.data().totalAmount, 0);

	let grandTotal = orderPrice-totalMilleage-totalRoadtall-totalFuel-totalGarage;

	return (
		<div className="container">
			<Card>
				<div className="d-md-flex justify-content-md-between">
					<div>
						<img src="/img/logo.png" alt=""/>
						<address>
							<p>
								<span className="font-weight-semibold text-dark font-size-md">Great lakes</span><br />
								<span>Phone: 0788123567</span><br /><br />
								<span className="font-weight-semibold text-dark font-size-md">Client: {order.client}</span><br />
								<span>Number: {order.customerNumber}</span><br />
								<span>Truck: {trip.internal ? `${allTrucks[trip.truckId].plateNumber} - ${allTrucks[trip.truckId].driverName}` : `${allRentedTrucks[trip.truckId].plateNumber} - ${allRentedTrucks[trip.truckId].driverName}`}</span><br />
								<span>From: {allDestinationsLoading ? '' : allDestinations[trip.departure]?.name +' - '+allDestinations[trip.departure]?.country}</span><br />
								<span>To: {allDestinationsLoading ? '' : allDestinations[trip.destination]?.name +' - '+allDestinations[trip.destination]?.country}</span><br />
								<span>Item: {order.item}</span><br />
								<span>Milleage: {trip.milleage}</span><br />
								<span>Quantity: {trip.quantity}</span><br />
								<span>Price/Ton: {trip.tonPrice} {` ${allCurrencyLoading ? '' : allCurrency[trip?.currency]?.name}`}</span><br />
								<span>Rate: {trip.exchangeRate}</span><br />
								<span>Total RWF: {trip.totalAmount}</span><br />
							</p>
						</address>
					</div>
					<div className="mt-3 text-right">
						<h2 className="mb-1 font-weight-semibold">Trip Details</h2>
						<p>{new Date(order.date).toLocaleDateString()}</p>
						<address>
							<p>
								
							</p>
						</address>
					</div>
				</div>

				<div className="mt-4">
					<h2 className="mb-1 font-weight-semibold">Road Talls</h2>
					<Table dataSource={roadTalls} pagination={false} className="mb-5">
						<Column title="Date" dataIndex="date" key="date" render={(_,record) => new Date(+record.data()._id).toLocaleDateString() }/>
						
						
						<Column title="Truck" 
							render={ (_,record) => record.data().internal ? 
							`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
							:
							`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`}
							key="truckId"
						/>				

						<Column title="Border" 
							render={(_,record) => `${allBorders[record.data().border]?.name} - ${allBorders[record.data().border]?.country}`} 
							key="border"
						/>		

						<Column 
							title="Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().amount} 
									suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						<Column 
							title="Rate" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate} 
									thousandSeparator={true} 
								/>
							)} 
							key="rate"
						/>
						<Column 
							title="Total RWF" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate*record.data().amount} 
									suffix={' RWF'} 
									thousandSeparator={true} 
								/>
							)} 
							key="totalAmount"
						/>
						
					</Table>
				</div>

				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Services</h2>
					<Table dataSource={orderGarage} pagination={false} className="mb-5">
						<Column title="Date" dataIndex="date" key="date" render={(_,record) => new Date(+record.data()._id).toLocaleDateString() }/>
						
						
						<Column title="Truck" 
							render={ (_,record) => `${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`}
							key="truckId"
						/>		

						<Column title="Description" 
							render={(_,record) => record.data().description}
							key="description"
						/>

						<Column 
							title="Price" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().amount} 
									suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
									thousandSeparator={true} 
								/>
							)} 
							key="price"
						/>
						<Column 
							title="Rate" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate} 
									thousandSeparator={true} 
								/>
							)} 
							key="rate"
						/>
						<Column 
							title="Total RWF" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().totalAmount} 
									suffix={' RWF'} 
									thousandSeparator={true} 
								/>
							)} 
							key="totalAmount"
						/>
						
					</Table>
				</div>
				
				<div className="mt-3">
					<h2 className="mb-1 font-weight-semibold">Fuel Consumption</h2>
					<Table dataSource={orderFuel} pagination={false} className="mb-5">
						<Column title="Date" dataIndex="date" key="date" render={(_,record) => new Date(+record.data()._id).toLocaleDateString() }/>
						
						
						<Column title="Truck" 
							render={ (_,record) => record.data().internal ? 
							`${allTrucks[record.data().truckId].plateNumber} - ${allTrucks[record.data().truckId].driverName}`
							:
							`${allRentedTrucks[record.data().truckId].plateNumber} - ${allRentedTrucks[record.data().truckId].driverName}`}
							key="truckId"
						/>			

						<Column 
							title="Liters" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().liters} 
							  		suffix={'L'} 
									thousandSeparator={true} 
								/>
							)} 
							key="liters"
						/>

						<Column 
							title="Price/L" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().literPrice} 
									suffix={` ${allCurrencyLoading ? '' : allCurrency[record.data().currency]?.name}`} 
									thousandSeparator={true} 
								/>
							)} 
							key="literPrice"
						/>
						<Column 
							title="Rate" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().exchangeRate} 
									thousandSeparator={true} 
								/>
							)} 
							key="rate"
						/>
						<Column 
							title="Total RWF" 
							render={(_,record) => (
								<NumberFormat
									displayType={'text'} 
									value={record.data().totalAmount} 
									suffix={' RWF'} 
									thousandSeparator={true} 
								/>
							)} 
							key="totalAmount"
						/>
						
						<Column title="Station" 
							render={(_,record) => record.data().station}
							key="station"
						/>

						<Column title="Country" 
							render={(_,record) => allCountriesLoading ? '' :  allCountries[record.data().country]?.name }
							key="country"
						/>

					</Table>
					<div className="d-flex justify-content-end mt-4">
						<div className="text-right ">
							<div className="border-bottom">
								<p className="mb-2">
									<span>Trip Revenue: </span>
									<NumberFormat 
										displayType={'text'} 
										value={trip.totalAmount} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								<p className="mb-2">
									<span>Milleages Used: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalMilleage} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								
								<p className="mb-2">
									<span>Roadtall Paid: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalRoadtall} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								
								<p className="mb-2">
									<span>Fuel Consumption: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalFuel} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
								
								<p className="mb-2">
									<span>Service: </span>
									<NumberFormat 
										displayType={'text'} 
										value={totalGarage} 
										suffix={'Rwf'} 
										thousandSeparator={true}
									/>
								</p>
							</div>
							<h2 className="font-weight-semibold mt-3">
								<span className="mr-1">Grand Total: </span>
								<NumberFormat 
									displayType={'text'} 
									value={rounder(grandTotal)} 
									suffix={'Rwf'} 
									thousandSeparator={true}
								/>
							</h2>
						</div>
					</div>
				</div>

				

				<hr className="d-print-none"/>
				<div className="text-right d-print-none">
					<Button type="secondary" className="mr-2" onClick={() => RedirectBack()}>
						<ArrowLeftOutlined /> Back
					</Button>
					<Button type="primary" className={`mr-2 ${userData?.role === 'Operator' ? 'd-none' : ''}`} onClick={() => window.print()}>
						<PrinterOutlined  type="printer" />
						<span className="ml-1">Print</span>
					</Button>
				</div>

			</Card>
		</div>
	);
}

export default Invoice
